.infoBox{
    flex:1;
    cursor:pointer;
}
.infoBox:not(:last-child){
    margin-right: 15px;
}
.infoBox_cases{
    color:#cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.infoBox_total{
    color:#6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-bottom:15px !important;
}
.infoBox--selected{
    border-top:10px solid greenyellow;
}
.infoBox--red{
    border-color: red;
}
.infoBox_cases--green{
    color:lightgreen !important;
}